<template lang="pug">
  #page.screen.page
    .page-container(v-if="blog")
      .page-wrapper
        .cover(v-if="blog.img")
          .cover-image(:style="{backgroundImage:'url('+blog.img+')'}")
            .title {{blog.title}}
              .meta
                a.category(v-for="e in blog.labels", :style="{'background-color':'#'+e.color}") {{e.name}}
        .title(v-else) {{blog.title}}
          .meta
            a.category(v-for="e in blog.labels", :style="{'background-color':'#'+e.color}") {{e.name}}
        .content(v-html="blog.body")
        Comments(:config="config",:id="$route.params.id")
        form#comment-form(methods="post",action="#",role="form")
          .send-comment
            .respond
              textarea.textarea(rows=8,name="body",placeholder="来一发？",:disabled="!login")
              a.send(:class="{'dissend':!login}")
                svg(style="width:50px;height:20px",viewBox="0 0 24 24")
                  path(fill="#666666",d="M2,21L23,12L2,3V10L17,12L2,14V21Z")
          .extra(v-if="login")
            .text-info(style="background-color:#20bf20;color:white;") 登录身份
          .extra(v-else)
            .text-info(style="background-color:#f0ad4e;color:white;") 请登录到 GITHUB 尊享评论功能 
              a(:href="'https://github.com/login/oauth/authorize?client_id=57e324589d9c66aab37e&redirect_uri=http://godgov.cn/#/page/' + $route.params.id ") 登录>>
</template>

<script>
import Comments from "@/components/Comments";
export default {
  name: "Page",
  data() {
    return {
      blog: false
    };
  },
  created() {
    if (this.lists) {
      this.changeList();
    }
  },
  watch: {
    lists: function() {
      this.changeList();
    }
  },
  methods: {
    changeList: function() {
      var that = this;
      this.lists.forEach(function(e) {
        if (e.id == that.$route.params.id) {
          that.blog = e;
          return false;
        }
      });
    }
  },
  components: {
    Comments: Comments
  },
  props: ["lists", "config","login"]
};
</script>
<style>
</style>



